/**
 * import 3rd party libs
 */

import angular from 'angular';

/**
 * import custom components
 */

import QuoteEmailModalController from './quote-email-modal.controller';

/**
 * import custom configs
 */

import routes from './quote-email-modal.route';
import enLang from './lang/en'
import deLang from './lang/de'

angular.module('quote-email-modal.component', [])
    .controller('QuoteEmailModalController', QuoteEmailModalController)
    .config(['componentManagerProvider', function(componentManagerProvider) {
        componentManagerProvider.registerRoutes(routes)
        componentManagerProvider.registerLocalizations('quote-email-modal.component', 'en', enLang);
        componentManagerProvider.registerLocalizations('quote-email-modal.component', 'de', deLang);
    }]);
