import angular from 'angular';

import topMenuDirective from './top-menu.directive';
import enLang from './lang/en';
import deLang from './lang/de';

angular.module('top-menu.component', [])
    .directive('wrkTopMenu', topMenuDirective)
    .config(['componentManagerProvider', function(componentManagerProvider) {
        componentManagerProvider.registerLocalizations('top-menu.component', 'en', enLang);
        componentManagerProvider.registerLocalizations('top-menu.component', 'de', deLang);
    }]);

