/**
 * import 3rd party libs
 */

import angular from 'angular';

/**
 * import custom components
 */

import QuoteItemsPanelController from './quote-items-panel.controller';
import QuoteItemsPanelDirective from './quote-items-panel.directive'

/**
 * import custom configs
 */

import enLang from './lang/en'
import deLang from './lang/de'

angular.module('quote-items-panel.component', [])
    .controller('QuoteItemsPanelController', QuoteItemsPanelController)
    .config(['componentManagerProvider', function(componentManagerProvider) {
        componentManagerProvider.registerLocalizations('quote-items-panel.component', 'en', enLang);
        componentManagerProvider.registerLocalizations('quote-items-panel.component', 'de', deLang);
    }])
    .directive('wrkQuoteItemsPanel', QuoteItemsPanelDirective);;
