/**
 * import 3rd party libs
 */

import angular from 'angular';

/**
 * import custom components
 */

import QuoteContactsPanelController from './quote-contacts-panel.controller';
import QuoteContactsPanelDirective from './quote-contacts-panel.directive'

/**
 * import custom configs
 */

import enLang from './lang/en'
import deLang from './lang/de'

angular.module('quote-contacts-panel.component', [])
    .controller('QuoteContactsPanelController', QuoteContactsPanelController)
    .config(['componentManagerProvider', function(componentManagerProvider) {
        componentManagerProvider.registerLocalizations('quote-contacts-panel.component', 'en', enLang);
        componentManagerProvider.registerLocalizations('quote-contacts-panel.component', 'de', deLang);
    }])
    .directive('wrkQuoteContactsPanel', QuoteContactsPanelDirective);
