import ControlInputController from './control-input.controller';

/**
 *   Usage:
 *   <fm-control-input  label="ctrl.myLabel"
 *                      model="ctrl.myModel">
 *   </fm-control-input>
 */

class ControlInput {
    constructor() {
        return {
            bindToController: {
                label: '@',
                model: '='
            },
            controllerAs: 'controlInput',
            controller: ControlInputController,
            restrict: 'E',
            scope: true,
            templateUrl: 'blocks/control-input/control-input.template.html'
        };
    }
}

function controlInputDirective() {
    return new ControlInput();
}

controlInputDirective.$inject = [];

export default controlInputDirective;
