/**
 * import 3rd party libs
 */

import angular from 'angular';

/**
 * import custom components
 */

import QuoteCommentsPanelController from './quote-comments-panel.controller';
import QuoteCommentsPanelDirective from './quote-comments-panel.directive'

/**
 * import custom configs
 */

import enLang from './lang/en'
import deLang from './lang/de'

angular.module('quote-comments-panel.component', [])
    .controller('QuoteCommentsPanelController', QuoteCommentsPanelController)
    .config(['componentManagerProvider', function(componentManagerProvider) {
        componentManagerProvider.registerLocalizations('quote-comments-panel.component', 'en', enLang);
        componentManagerProvider.registerLocalizations('quote-comments-panel.component', 'de', deLang);
    }])
    .directive('wrkQuoteCommentsPanel', QuoteCommentsPanelDirective);;
