class FooterController {
    constructor($scope) {
        const vm = this;
        vm.$scope = $scope;
    }
    action() {
        const vm = this;
        if (vm.onAction) {
            vm.onAction(arguments);
        }
    }
    cancel() {
        const vm = this;
        if (vm.onCancel) {
            vm.onCancel(arguments);
        }
    }
}

FooterController.$inject = ['$scope'];

export default FooterController;