import SearchPanelController from './search-panel.controller';

class SearchPanel {
    constructor() {
        return {
            bindToController: {
                value: '@',
                onSearch: '&'
            },
            controller: SearchPanelController,
            controllerAs: 'searchPanel',
            restrict: 'E',
            replace: true,
            scope: true,
            templateUrl: 'components/search-panel/search-panel.template.html'
        }
    }
}

function searchPanelDirective() {
    return new SearchPanel(arguments);
}

searchPanelDirective.$inject = [];

export default searchPanelDirective;