let me = {};

class QuoteContactsPanelController {

    constructor(dataRepository) {
        me = this;
        this.controlData = {};

        this.test = []
    }

    filterSites(site) {

        if (me.selectedContact) {
            me.filterCount = 0;
            return function(item, index, array) {
                let found = false;
                angular.forEach(me.selectedContact.sites, function(site) {
                    if (!found && site.id === item.id) {
                        found = true;
                        me.filterCount++;
                    }
                });
                return found;
            };
        }
        else {
            return function() {
                return false;
            }
        }
    }

    resetSelectedSite() {
        this.selectedSite = null;
    }
}

QuoteContactsPanelController.$inject = ['dataRepository'];

export default QuoteContactsPanelController
