/**
 * import 3rd party libs
 */

import angular from 'angular';

/**
 * import custom components
 */

import QuoteEmailModalAttachmentsPanelController from './quote-email-modal-attachments-panel.controller';
import QuoteEmailModalAttachmentsPanelDirective from './quote-email-modal-attachments-panel.directive'

/**
 * import custom configs
 */

import enLang from './lang/en'
import deLang from './lang/de'

angular.module('quote-email-modal-attachments-panel.component', [])
    .controller('QuoteEmailModalAttachmentsPanelController', QuoteEmailModalAttachmentsPanelController)
    .config(['componentManagerProvider', function(componentManagerProvider) {
        componentManagerProvider.registerLocalizations('quote-email-modal-attachments-panel.component', 'en', enLang);
        componentManagerProvider.registerLocalizations('quote-email-modal-attachments-panel.component', 'de', deLang);
    }])
    .directive('wrkQuoteEmailModalAttachmentsPanel', QuoteEmailModalAttachmentsPanelDirective);;
