/**
 * import 3rd party libs
 */

import angular from 'angular';

/**
 * import custom components
 */

import QuoteDetailsPanelController from './quote-details-panel.controller';
import QuoteDetailsPanelDirective from './quote-details-panel.directive'

/**
 * import custom configs
 */

import enLang from './lang/en'
import deLang from './lang/de'

angular.module('quote-details-panel.component', [])
    .controller('QuoteDetailsPanelController', QuoteDetailsPanelController)
    .config(['componentManagerProvider', function(componentManagerProvider) {
        componentManagerProvider.registerLocalizations('quote-details-panel.component', 'en', enLang);
        componentManagerProvider.registerLocalizations('quote-details-panel.component', 'de', deLang);
    }])
    .directive('wrkQuoteDetailsPanel', QuoteDetailsPanelDirective);
