import angular from 'angular';

import quoteActionHeaderDirective from './quote-actions-header.directive';
import enLang from './lang/en';
import deLang from './lang/de';

angular
    .module('quote-actions-header.component', [])
    .directive('quoteActionsHeader', quoteActionHeaderDirective)
    .config(['componentManagerProvider', function(componentManagerProvider) {
        componentManagerProvider.registerLocalizations('quote-actions-header.component', 'en', enLang);
        componentManagerProvider.registerLocalizations('quote-actions-header.component', 'de', deLang);
    }]);