import angular from 'angular';

import searchPanelDirective from './search-panel.directive';
import enLang from './lang/en';
import deLang from './lang/de';

angular
    .module('search-panel.component', [])
    .directive('wrkSearchPanel', searchPanelDirective)
    .config(['componentManagerProvider', function(componentManagerProvider) {
        componentManagerProvider.registerLocalizations('search-panel.component', 'en', enLang);
        componentManagerProvider.registerLocalizations('search-panel.component', 'de', deLang);
    }]);
