import TopMenuController from './top-menu.controller';

class TopMenu {
    constructor() {
        return {
            bindToController: {},
            controller: TopMenuController,
            controllerAs: 'vm',
            restrict: 'E',
            replace: true,
            scope: true,
            templateUrl: 'components/top-menu/top-menu.template.html'
        }
    }
}

function topMenuDirective() {
    return new TopMenu(arguments);
}

topMenuDirective.$inject = [];

export default topMenuDirective;