/**
 * import 3rd party libs
 */

import angular from 'angular';
import 'js-data';
import 'js-data-localstorage';
import 'js-data-angular';

/**
 * import custom components
 */

import dataRepositoryProvider from './data-repository.provider'

/**
 * import custom exports
 */

import quoteModel from './dynamic/quote/quote.model'
import configuration from '../configuration.app'

angular.module('data-repository.component', [

/**
 * inject 3rd party components
 */

    'js-data'

/**
 *  inject custom components
 */

])
    .provider('dataRepository', dataRepositoryProvider)
    .config(['dataRepositoryProvider', function(dataRepositoryProvider) {
        console.log('data-repository.component config');

        dataRepositoryProvider.registerDataStores(configuration);

        const dynamicRepositories = [
            {
                name: 'quote',
                repo: quoteModel
            }
        ];

        dataRepositoryProvider.registerDynamicRepositories(dynamicRepositories);

        const lookupRepositories = [

        ];

        dataRepositoryProvider.registerLookupRepositories(lookupRepositories);

        dataRepositoryProvider.initRepositories();
    }]);
