/**
 * import 3rd party libs
 */

import angular from 'angular';

/**
 * import custom components
 */

import QuoteEmailModalRecipientPanelController from './quote-email-modal-recipient-panel.controller';
import QuoteEmailModalRecipientPanelDirective from './quote-email-modal-recipient-panel.directive'

/**
 * import custom configs
 */

import enLang from './lang/en'
import deLang from './lang/de'

angular.module('quote-email-modal-recipient-panel.component', [])
    .controller('QuoteEmailModalRecipientPanelController', QuoteEmailModalRecipientPanelController)
    .config(['componentManagerProvider', function(componentManagerProvider) {
        componentManagerProvider.registerLocalizations('quote-email-modal-recipient-panel.component', 'en', enLang);
        componentManagerProvider.registerLocalizations('quote-email-modal-recipient-panel.component', 'de', deLang);
    }])
    .directive('wrkQuoteEmailModalRecipientPanel', QuoteEmailModalRecipientPanelDirective);;
