/**
 * import 3rd party libs
 */

import angular from 'angular';

/**
 * import custom components
 */

import QuoteDocumentsPanelController from './quote-documents-panel.controller';
import QuoteDocumentsPanelDirective from './quote-documents-panel.directive'

/**
 * import custom configs
 */

import enLang from './lang/en'
import deLang from './lang/de'

angular.module('quote-documents-panel.component', [])
    .controller('QuoteDocumentsPanelController', QuoteDocumentsPanelController)
    .config(['componentManagerProvider', function(componentManagerProvider) {
        componentManagerProvider.registerLocalizations('quote-documents-panel.component', 'en', enLang);
        componentManagerProvider.registerLocalizations('quote-documents-panel.component', 'de', deLang);
    }])
    .directive('wrkQuoteDocumentsPanel', QuoteDocumentsPanelDirective);
